import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import { fade, makeStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import './Home.css'
import Button from '@material-ui/core/Button';
import { useState } from 'react';
import HomeIcon from '@material-ui/icons/Home';
import {Link,browserHistory,IndexRoute,BrowserRouter as Router,Route,Switch} from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'block',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '60%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
  },
}));


export default function SearchAppBar() {
  const classes = useStyles();
  const [search,setsearch] = useState('')

  return (
    <div className={classes.root}>
        <Router browserHistory>
      <AppBar position="static">
        <Toolbar>
        <Link to='/'><Button style={{marginLeft:'-20px',color:'white'}}  onClick="window.location.href=window.location.href" > 
              <HomeIcon />
              </Button></Link>
          <Typography className={classes.title} variant="h6" noWrap>
            Daily News
          </Typography>
          <Link to='/Search'><Button variant="contained" onClick="window.location.href=window.location.href" color="secondary" style={{marginLeft:'10px'}}>Search</Button></Link>
        </Toolbar>
      </AppBar>
      </Router>
    </div>
  );
}
